import { Address } from '@ton/core';
import { beginCell } from '@ton/core';

export const toNano = (amount: number) => BigInt(Math.floor(amount * 1e9)).toString();

export const getJettonTransferPayload = (recipient: string, amountNano: string) => {
  const OP_TRANSFER = 0xf8a7ea5;

  const payload = [
    OP_TRANSFER.toString(16).padStart(8, '0'),
    '0000000000000000',
    BigInt(amountNano).toString(16).padStart(32, '0'),
    Address.parse(recipient).toString({ bounceable: true }).replace('0:', '').padStart(64, '0'),
    '0000000000000000',
    '00',
    '00000000',
    '00'
  ].join('');

  return `0x${payload}`;
};

// Новый способ: Jetton Wallet адрес вычисляется через стандарт Cell формулу (без SDK функции)
export const getJettonWalletAddress = (userAddress: string, jettonMasterAddress: string): string => {
  const owner = Address.parse(userAddress);
  const master = Address.parse(jettonMasterAddress);

  const data = beginCell()
    .storeAddress(master)
    .storeAddress(owner)
    .endCell();

  const hash = data.hash();
  const wc = 0; // обычно workchain 0
  const walletAddress = new Address(wc, hash);

  return walletAddress.toString({ testOnly: false });
};